import React, { useContext } from "react";
import { GlobalContext } from "../context/GlobalState";
import { MovieCard } from "./MovieCard";

export const Watchlist = () => {
  const { watchlist } = useContext(GlobalContext);

  return (
    <div className="movie-page">
      <div className="container">
        <div className="header">
          <h1 className="heading">My watchlist</h1>
        </div>
        <div className="header-count-pill">
          <div className="count-pill">
            {watchlist.length === 0
              ? `There are no movies on your list.`
              : watchlist.length === 1
              ? `There is only ${watchlist.length} movie on your list.`
              : `There are ${watchlist.length} movies in your list. Enjoy watching!`}
          </div>
        </div>

        {watchlist.length > 0 ? (
          <div className="movie-grid">
            {watchlist.map((movie) => (
              <MovieCard movie={movie} type="watchlist" />
            ))}
          </div>
        ) : (
          <h2 className="no-movies">
            It's quiet here. Go to "add movie" to add some movies!
          </h2>
        )}
      </div>
    </div>
  );
};
