import React, { useContext } from "react";
import { GlobalContext } from "../context/GlobalState";
import { MovieCard } from "./MovieCard";

export const Watched = () => {
  const { watched } = useContext(GlobalContext);
  return (
    <div className="movie-page">
      <div className="container">
        <div className="header">
          <h1 className="heading">Watched movies</h1>
        </div>
        <div className="header-count-pill">
          <div className="count-pill">
            {watched.length === 0
              ? `There are no movies on your list.`
              : watched.length === 1
              ? `There is only ${watched.length} movie on your list.`
              : `There are ${watched.length} movies in your list.`}
          </div>
        </div>

        {watched.length > 0 ? (
          <div className="movie-grid">
            {watched.map((movie) => (
              <MovieCard movie={movie} type="watched" />
            ))}
          </div>
        ) : (
          <h2 className="no-movies">You haven't seen any movies yet.</h2>
        )}
      </div>
    </div>
  );
};
