import React from "react";
import { Link } from "react-router-dom";
import { Button } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import TheatersIcon from "@mui/icons-material/Theaters";

export const Header = () => {
  return (
    <header>
      <div className="container">
        <div className="inner-content">
          <div className="brand">
            <Link to="/">
              <div className="header-title">
                <TheatersIcon className="title-icon" fontSize="large" />
                Movie database
              </div>
            </Link>
          </div>

          <ul className="nav-links">
            <li>
              <Link to="/" className="btn-add-movie">
                Latest movies
              </Link>
            </li>

            <li>
              <Link to="/watchlist" className="btn-add-movie">
                Watch List
              </Link>
            </li>

            <li>
              <Link to="/watched" className="btn-add-movie">
                Watched
              </Link>
            </li>

            <li>
              <Link to="/add">
                <Button variant="contained" color="success">
                  Add Movie
                  <AddIcon />
                </Button>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </header>
  );
};
