import React from "react";

const IMG_API = "https://image.tmdb.org/t/p/w1280";

export const Movie = ({
  title,
  poster_path,
  overview,
  vote_average,
  release_date,
}) => (
  <div className="result-card movie">
    <div className="poster-wrapper">
      <img
        src={
          poster_path
            ? IMG_API + poster_path
            : "https://images.unsplash.com/photo-1512412632857-b93f6ecce335?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80"
        }
        alt={title}
      />
    </div>
    <div className="info">
      <div className="movie-card-header">
        <div className="movie-header">
          <h3 className="title">{title}</h3>
          <span className="vote-average">{vote_average}</span>
        </div>
        <div className="release-date">
          {release_date
            ? `This movie was released in ${release_date.substring(0, 4)}`
            : "No release date"}
        </div>
        <div className="movie-over">
          <h2>Overview</h2>
          <p>{overview}</p>
        </div>
      </div>
    </div>
  </div>
);

export default Movie;
