import React, { useState, useEffect } from "react";
import Movie from "./Movie";

const FEATURED_API =
  "https://api.themoviedb.org/3/discover/movie?sort_by=popularity.desc&api_key=071148fb28ba2fcf7575e6c281c7647a&page=1";

export const Latestmovies = () => {
  const [movies, setMovies] = useState([]);
  useEffect(() => {
    getMovies(FEATURED_API);
  }, []);

  const getMovies = (API) => {
    fetch(API)
      .then((res) => res.json())
      .then((data) => {
        // console.log(data);
        setMovies(data.results);
      });
  };

  return (
    <div className="movie-page">
      <div className="container">
        <div className="header">
          <h1 className="heading">Latest movies on cinema</h1>
        </div>
        <div className="add-content">
          {movies.length > 0 && (
            <ul className="results">
              {movies.map((movie) => (
                <li key={movies.id}>
                  <Movie {...movie} />
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
    </div>
  );
};
